<template>
    <div class="titles">
        <h2>方法命名</h2>
        <p>插件实际的实现是在 Vue 的原型链上添加了三个基础方法，为了排除命名冲突的问题，我们提供了修改命名的方法</p>
        <p>插件在 main.js 中注册的时候，可传递一个 rename 字段，该字段的值必须是一个对象。</p>
        <pre><code>let plugOptions = {
  mapboxgl,router,store,
  rename:{
    &#39;$addMarker&#39;:&#39;addMK&#39;,
    &#39;$removeMarker&#39;:&#39;delMk&#39;,
    &#39;$makeMarker&#39;:&#39;makeMK&#39;
  }
}

Vue.use(mapboxvuemarker,plugOptions)
</code></pre>
        <p>上述代码可以看出，键名为要修改的方法，键值为修改的结果</p>

    </div>
</template>

<script>
    export default {
        name: "doc-rename"
    }
</script>

