<template>
    <div class="titles">
        <h2>创建marker</h2>
        <p>在配置好环境以后，我们正式开始创建marker，以下是一个创建marker的小例子
            首先我们创建一个基本的 vue 组件，假设它的名字叫做 vuemarker</p>
        <pre><code class='language-vue' lang='vue'>&lt;template&gt;
    &lt;div class=&quot;vuemarker&quot;&gt;V&lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
    export default {
        name: &quot;vuemarker&quot;,
    }
&lt;/script&gt;
</code></pre>
        <p>可以看到，完全只是一个普通的 vue 对象，现在我们把这个对象导出到需要进行扎点的逻辑的组件中</p>
        <pre><code>mapboxgl.accessToken = &#39;token&#39;;
var map = new mapboxgl.Map({
    container: &#39;map&#39;,
    style: &#39;mapbox://styles/mapbox/streets-v11&#39;,
    center: [-74.50, 40],
    zoom: 9
});
this.marker = this.$makeMarker({
    lnglat:map.getCenter(),
    component:vuemarker,
});
</code></pre>
        <p>上述代码创建了一个最基本的mapbox地图，然后通过插件的全局方法 $makeMarker 方法创建了一个marker对象，并赋值给 this.marker </p>
        <p>构建marker的方法完全封装在插件内部，对外之暴露入口方法，一个最基本的marker对象仅需要传入经纬度和 vue 组件即可完成</p>
        <p>$makeMarker的详细配置参数有：</p>
        <pre><code>options = {
    lnglat,     //marker在地图上的经纬度（必须）
    component,  //组成marker的 vue 组件
    anchor,     //marker的中心点，详情见mapbox官网介绍，默认为bottom
    props,      //vue组件接收的参数对象，默认为空对象
    draggable,  //marker是否可拖动，默认为false
    zIndex,     //扎点的层级，默认为空
}
</code></pre>

    </div>
</template>

<script>
    export default {
        name: "doc-makemarker"
    }
</script>

