<template>
    <div class="titles">
        <h2>行为分析</h2>
        <ol>
            <li>现行的版本中，您可以直接在 vue 组件中获取到正确的 router 和 vuex</li>
            <li>$addMarker 和 $removeMarker 仅对与通过 $makeMarker 方法构建的marker生效</li>
            <li>$makeMarker 构造完毕后，会立刻执行 vue 组件的 mounted 方法，并且将一直处于挂载状态</li>
            <li>我们默认向 vue 组件提供了两个 props ，分别是 marker 和 parent 对象，parent 代表调用构造 marker 方法的 vue 实例</li>
            <li>我们默认托管了 marker 的层级关系，但是这个层级并不是固定的，它在每次拖动地图时，都会进行计算得出结果，并设置为对应的层级，您可以在构建 marker 时传入 zIndex 来设置为固定层级</li>
            <li>考虑到业务中的逻辑，我们提供了 $addMarker 和 $removeMarker 的回调方法，您可以在 vue 组件的 methods 中提供 onAdd 和 onRemove 方法，它们将作为回调方法自动调用</li>
            <li>mapbox 的 marker 也具有默认行为，当您调用 $addMarker 的时候，它将首先调用一次 remove方法，您需要注意调用回调时，可能产生不可预知的错误</li>

        </ol>
        <p>&nbsp;</p>

    </div>
</template>

<script>
    export default {
        name: "doc-react"
    }
</script>

