<template>
    <div class="titles">
        <h2>数据存储</h2>
        <p>在实际项目中，更多可能的是会面临多种类多数量的marker</p>
        <p>在此提供一种保存数据的想法</p>
        <pre><code>export default {
    name: &#39;home&#39;,
    data:function(){
        return {
            lnglatBox:[
                [-74.0, 40],
                [-75.50, 40],
                [-74.5, 40.2],
                [-74.95, 40.2],
                [-74.95, 40]
            ],
            markerBox:{
            	markerType:[]
            },
            marker:null
        }
    },
    mounted(){
        mapboxgl.accessToken = &#39;token&#39;;
        var map = new mapboxgl.Map({
            container: &#39;map&#39;, // container id
            style: &#39;mapbox://styles/mapbox/streets-v11&#39;,
            center: [-74.50, 40],
            zoom: 9
        });

        this.marker = this.$makeMarker({
            lnglat:map.getCenter(),
            component:vuemarker,
        });
        for(let i in this.lnglatBox){
            let marker = this.$makeMarker({
                lnglat:this.lnglatBox[i],
                component:vuemarker,
            });
            this.markerBox.markerType.push(marker);
        }
        map.on(&#39;load&#39;,() =&gt; {
            this.$addMarker(this.marker,map)
            this.$addMarker(this.markerBox,map)
            this.$removeMarker(this.marker)
        })
    },
}
</code></pre>
        <p>将不同的marker分类存放在markerbox对象中，集中托管</p>
        <p>&nbsp;</p>

    </div>
</template>

<script>
    export default {
        name: "doc-savedata"
    }
</script>

