<template>
    <div class="right" ref="right">
        <article class="markdown-body">
            <component name="props" :is="article"></component>
        </article>
    </div>
</template>

<script>
    import atc_install from '@/components/Articles/doc-install'
    export default {
        name: "Right",
        data:function(){
            return {
                article:atc_install
            }
        },
        mounted() {
            let blocks = this.$el.querySelectorAll('pre code');
            blocks.forEach((block)=>{
                hljs.highlightBlock(block)
            })
            this.bus.$on('changeArticle',(params) => {
                this.article = params.article;
                this.$nextTick(() => {
                    let blocks = this.$el.querySelectorAll('pre code');
                    blocks.forEach((block)=>{
                        hljs.highlightBlock(block)
                    })
                    this.$refs.right.scroll(0,0)
                    // window.hljs.initHighlightingOnLoad();
                })
            })
        }
    }
</script>

<style scoped lang="scss">
.right{
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    padding-bottom: 100px;
    width: 100%;
    min-width: 960px;
    scroll-behavior:smooth;
    padding-right: 260px;
    .markdown-body {
        box-sizing: border-box;
        min-width: 200px;
        max-width: 980px;
        margin: 0 auto;
        padding: 45px;
    }

    @media (max-width: 767px) {
        .markdown-body {
            padding: 15px;
        }
    }
}
</style>