<template>
    <div class="titles">
        <h2>添加marker到地图</h2>
        <p>通过$makeMarker方法创建的marker不会自动添加到地图</p>
        <p>此时还需要调用$addMarker方法来添加marker到地图</p>
        <pre><code>this.$addMarker(this.marker,map)
</code></pre>
        <p>$addMarker的两个参数都为必填项</p>
        <p>第一个参数可以是marker对象，也可以是包含marker的数组或对象，第二个参数为map对象</p>

    </div>
</template>

<script>
    export default {
        name: "doc-addtomap"
    }
</script>

