<template>
    <div class="titles">
        <h2>注册组件</h2>
        <p>通过 npm 安装之后，需要在 main.js 注册该插件</p>
        <pre><code>import store from &#39;./store&#39;
import mapboxgl from &#39;mapbox-gl&#39;
import &#39;mapbox-gl/dist/mapbox-gl.css&#39;
import mapboxvuemarker from &#39;mapbox-vue-marker&#39;

let plugOptions = {
  mapboxgl,router,store,
  // 可通过rename修改默认的方法名
  // rename:{
  //   &#39;$addMarker&#39;:&#39;addMK&#39;,
  //   &#39;$removeMarker&#39;:&#39;delMk&#39;,
  //   &#39;$makeMarker&#39;:&#39;makeMK&#39;
  // }
}

Vue.use(mapboxvuemarker,plugOptions)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h =&gt; h(App)
}).$mount(&#39;#app&#39;)
</code></pre>
        <p>通过 Vue 提供的use方法注册该组件，传入插件对象和配置对象</p>
        <p>配置对象的参数如下</p>
        <pre><code>mapboxgl,	//mapboxgl对象（必须）
router,		//vue的router（可选）
store,		//vuex对象（可选）
/*
* 插件会提供三个全局方法，三个方法有默认的方法名
* rename对象的作用是修改默认的方法名
*/
rename:{
    &#39;$addMarker&#39;:&#39;addMK&#39;,
    &#39;$removeMarker&#39;:&#39;delMk&#39;,
    &#39;$makeMarker&#39;:&#39;makeMK&#39;
}
</code></pre>
        <p>到此插件的配置就完成了！</p>

    </div>
</template>

<script>
    export default {
        name: "doc-register"
    }
</script>

